



























































import {Component, Vue} from 'vue-property-decorator';

import Pagination from '@/components/Pagination/index.vue';

import {confirmDelete, success} from '@/utils/MessageUtils';
import {list, save, update, del} from './api';
import Data from '@/class/Data';
import Query from './Query';
import Obj from './BookType';
import Rule from '@/class/Rule';

@Component({
    components: {
        Pagination,
    },
})
export default class Book extends Vue {
    public $refs!: {
        dataForm: HTMLFormElement,
    };

    private data = new Data();
    private query = new Query();
    private obj = new Obj();
    private rules = {
        name: [new Rule({message: '请输入名称'})],
    };

    private created() {
        this.getList();
    }

    private getList() {
        this.data.tableLoading = true;
        list(this.query).then((resp) => {
            const {rows, total, pageSize} = resp.data;
            this.data.list = rows;
            this.query.total = total;
            this.query.pageSize = pageSize;

            this.data.tableLoading = false;
        });
    }

    private handleCreate() {
        this.data.dialogStatus = 'create';
        this.data.dialogFormVisible = true;
        this.$nextTick(() => {
            this.$refs.dataForm.resetFields();
            this.obj = new Obj();
        });
    }

    private create() {
        this.$refs.dataForm.validate((valid: boolean) => {
            if (valid) {
                const {name, memo} = this.obj;
                save({name, memo}).then((resp: any) => {
                    this.editSuccess(resp.data.message);
                });
            }
        });
    }

    private handleUpdate(row: any) {
        this.data.dialogStatus = 'update';
        this.data.dialogFormVisible = true;
        this.$nextTick(() => {
            this.$refs.dataForm.resetFields();
            const {id, name, memo} = row;
            this.obj = {id, name, memo};
        });
    }

    private update() {
        this.$refs.dataForm.validate((valid: boolean) => {
            if (valid) {
                update(this.obj).then((resp: any) => {
                    this.editSuccess(resp.data.message);
                });
            }
        });
    }

    private handleDelete(row: any) {
        const that = this;
        confirmDelete('删除提示', `${row.name}将要被删除`, '确定', '取消', () => {
            del(row.id).then((resp: any) => {
                success('成功', resp.data.message);
                that.getList();
            });
        });
    }

    private cancel() {
        this.data.dialogFormVisible = false;
        this.obj = new Obj();
    }

    private editSuccess(message: string) {
        success('成功', message);
        this.data.dialogFormVisible = false;
        this.obj = new Obj();
        this.getList();
    }
}
